.card {
  width: 100%;
  max-width: 300px;
  background: #f1f1f1;
  font-family: "verdana", sans-serif;
}

.top-section {
  background-color: turquoise;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 10px;
}

.title {
  font-size: 36px;
  font-weight: 700;
  color: #ffffff;
}

.subtitle {
  font-size: 18px;
  color: #ffffff;
}

.info-section {
  padding: 10px 30px;
  color: #333;
}

a {
  text-decoration: none;
  transition: 0.4s;
}
a:hover {
  color: white;
}

.buttonLink {
  display: block;
  font-size: 16px;
  color: #555555;
  padding: 10px 20px;
  margin: 5px 0;
  background-color: #30D5C833;
}
.buttonLink:hover,
.buttonLink:focus {
  color: #ffffff;
  background-color: #30D5C8FF;
}

a .icon {
  width: 20px;
  text-align: center;
  margin-right: 8px;
}
